import React, { Component } from 'react'
import { Button, Image } from 'react-bootstrap';
import { GET_NETWORK_ICON, GET_NETWORK_NAME, generateRandomArray } from '../../../../../utils';
import { baseUrl, FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { connect } from 'react-redux';

class ChooseNetwork extends Component {
    state = { addClass: true };

    render() {
        const { addFeedData, onNetworkSelect } = this.props
        const { addClass } = this.state
        return <div className='f-center m-auto add_feed_'>
            {addFeedData && Object.keys(addFeedData).length > 0 ?
                <div className='f-center flex-column mb-5'>
                    <div className="text-center mb-5">
                        <h3>Choose Source Network</h3>
                        <p>Select the social platform from which you want to add the feeds</p>
                    </div>
                    <div className="f-center flex-wrap">
                        {addFeedData.networks.slice(0, addClass ? 6 : addFeedData.networks.length).map((network, index) => (network.id != 33 && network.id != 29 ?
                            <div key={index} className="px-2 mb-4">
                                <Button variant='secondary' onClick={() => onNetworkSelect(network.id)}>
                                    <Image height={22} width={22} className='me-2' src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network.id)}.svg`)} alt={GET_NETWORK_NAME(network.id)} fluid />
                                    {network.name}
                                </Button>
                            </div> : null
                        ))}
                    </div>
                    {addFeedData.networks.length >= 5 && addClass ?
                        <div className="px-2 mb-4">
                            <Button variant='link' className='p-2 mt-2' onClick={() => this.setState({ addClass: !addClass })}>
                                <FontAwesomeIcon type={'solid'} icon={'circle-info'} />
                                Click here to see all supported Networks
                            </Button>
                        </div>
                        : null}
                </div>
                :
                <div className="f-center flex-wrap m-auto">
                    {generateRandomArray(10).map((number, index) => <div key={index} className="px-2 mb-4"><div className="sk_rect h-40px me-2 w-100 btn-min-w"></div></div>)}
                </div>}
        </div>
    }
}

const mapStateToProps = (state) => {
    return {
        addFeedData: state.addFeedReducer?.addFeedNetworks,
    }
}

export default connect(mapStateToProps)(ChooseNetwork)