import React, { Component } from 'react'
import Swal from 'sweetalert2'
import { Image, Nav, Form, Button, Tab, Alert } from 'react-bootstrap';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { CONNECT_SHOPIFY_STORE } from '../../../../../../actions/productAction.js'
import withRouter from '../../../../../routes/WithRouter.js'
import ImportProducts from '../../../ProductCatalog/Components/ImportProducts.js'
class SyncProduct extends Component {
    state = { uploadCsv: false, shopifyUrl: '', shopify_error: false }
    onMoreTab = () => {
        const { updateData } = this.props
        Swal.fire({
            title: "You have to connect your Account to use more options",
            icon: "info",
            showCancelButton: true,
            confirmButtonText: "I'll do it later",
            cancelButtonText: "Proceed"
        }).then((result) => {
            if (result.isDismissed && result.dismiss == "cancel") {
                updateData({ SKIP: true })
                this.props.navigate(`/content/products`)
            }
        });
    }

    render() {
        const { uploadCsv, shopifyUrl, shopify_error } = this.state
        return (<>
            <Tab.Container defaultActiveKey="shopify">
                <div className='d-flex flex-column add_feed_ m-auto'>
                    <div className="d-flex flex-sm-row flex-column mb-5 justify-content-between border-bottom pb-5">
                        <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pr-3">
                            <span className="fs-6 fw-bold text-gray-800">Sync Product</span>
                            <p className="fs-9 mb-0 fw-medium text-gray-500">You can select your preferred platform to sync your Products.</p>
                        </div>
                        <div className='d-flex align-items-center'>
                        <Button variant='link' onClick={() => this.setState({ uploadCsv: true })}>
                            <FontAwesomeIcon type={'solid'} icon={'download'} iconClass={'me-1'}/>
                            Import Product List
                        </Button>
                        </div>
                    </div>
                    <Nav variant="underline" className="mb-5 border-bottom align-items-center">
                            <Nav.Item>
                                <Nav.Link className='f-center' eventKey={`shopify`}>
                                    <Image className='me-2' height={20} width={20} src={baseUrl(`media/icons/platform-icons/shopify.svg`)} alt={'Shopify'} fluid />
                                    Shopify
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link className="nav-link" onClick={() => this.onMoreTab()}>More</Nav.Link>
                            </Nav.Item>
                    </Nav>
                    <Tab.Content>
                            <Tab.Pane eventKey={`shopify`}>
                                <div className="d-flex flex-sm-row flex-column mb-5 align-items-start">
                                    <div className="d-flex flex-grow-1 mb-3 w-100">
                                        <Form.Group className='w-100'>
                                            <Form.Control type="text" size='lg' className={`fw-semibold ${shopify_error?'is-invalid':''}`} placeholder='example.myshopify.com' value={shopifyUrl} onChange={(e) => this.setState({ shopifyUrl: e.target.value, shopify_error: false })} required/>
                                            {shopify_error ? <div className="invalid-feedback">This field is required</div> : null}
                                        </Form.Group>
                                    </div>
                                    <div className="d-flex flex-shrink-0 ps-sm-3 mb-3 justify-content-end">
                                        <Button className="btn border-0 d-flex signInWithBrand signIn-Shopify" value="shopify" onClick={() => { if (shopifyUrl) { localStorage.setItem("onboarding", true); CONNECT_SHOPIFY_STORE(shopifyUrl) } else this.setState({ shopify_error: true }) }}>
                                            <span className="h-42px w-42px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1 bg-white">
                                                <img src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="twitter" />
                                            </span>
                                            <div className="overflow-hidden signIn_txt px-2">
                                                <span className="text-gray-100-alt mb-0 w-100 fs-6 fw-medium text-ellipsis me-2">Connect Shop</span>
                                            </div>
                                        </Button>
                                    </div>
                                </div>
                            </Tab.Pane>
                    </Tab.Content>
                    <Alert variant="warning">
                        <Alert.Heading className='f-center alert_icon_head'>
                            <span>
                                <FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-5 me-2'}/>
                                Info
                            </span>
                        </Alert.Heading>
                        <p className="fw-medium mb-0">Tagshop only sync 500 products at a time, please reach out to <a href="mailto:support@tagshop.ai">support@tagshop.ai</a> for any query</p>
                    </Alert>
                </div>
            </Tab.Container>
            {uploadCsv ? <ImportProducts show={uploadCsv} onHide={() => this.setState({ uploadCsv: false })} countAllPage={this.refreshProducts} fromOB={true} navigate={this.props.navigate} /> : null}

        </>
        )
    }
}

export default withRouter(SyncProduct)