import React, { Component } from 'react'
import { Modal, Button } from 'react-bootstrap';
import { DragDrop } from "@uppy/react";
import { TOAST_LOADING } from '../../../../../utils';
import { IMPORT_CSV_PRODUCT } from '../../../../../actions/productAction';
import UppyUpload from '../../../../../utils/uppyUpload';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { Link } from 'react-router-dom';
import { TEMP_URL } from '../../../../../actions/constants';


export default class ImportProducts extends Component {
    state = { selectedFile: "", filename: "", selected: "", uploadedFileName: '', isLoading: false, loading: false }
    uppyUploader = new UppyUpload(['.csv'])

    componentDidMount() {
        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({ loading: false, selectedFile: result.id, filename: `${TEMP_URL}${result.id}.${result.ext}`, selected: `${TEMP_URL}${result.id}.${result.ext}`, uploadedFileName: result.name }, () => {
                setTimeout(() => { this.uppyUploader.uppy.cancelAll() }, 300)
            })
        })
        this.uppyUploader.uppy.on('file-added', (file) => this.setState({ loading: true }));
    }

    onUploadCSV = () => {
        const { filename } = this.state
        const { countAllPage, fromOB, navigate } = this.props
        var obj = new FormData()
        obj.append("csv_url", filename)
        if (navigate) IMPORT_CSV_PRODUCT(filename, this.onClose, countAllPage, fromOB, navigate);
        else IMPORT_CSV_PRODUCT(filename, this.onClose, countAllPage);
    }
    onClose = () => {
        this.setState({ selectedFile: "", filename: "", selected: "", uploadedFileName: '', isLoading: false, loading: false }, () => {
            this.uppyUploader.uppy.cancelAll()
            this.props.onHide()
        })
    }

    render() {
        const { show } = this.props
        const { loading, uploadedFileName } = this.state
        return <Modal show={show} onHide={this.onClose} size="md" id="ImportProducts" centered>
            <Modal.Header closeButton>
                <Modal.Title>Import Products by CSV</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <p className='mb-2 fw-medium'>Download the <Link id="download_csv" to={baseUrl('media/downloads/sample-products.csv')} target="_blank" className="btn-link" download>Sample CSV template&nbsp;</Link>
                    to see an example of the format required
                </p>
                <div className="w-100 mb-2">
                    <DragDrop
                        proudlyDisplayPoweredByUppy={false}
                        hideUploadButton={true}
                        showProgressDetails={true}
                        disableThumbnailGenerator={false}
                        // height={250}
                        width={'100%'}
                        style={{ padding: 0 }}
                        id="upload_csv"
                        // browserBackButtonClose={true}
                        showRemoveButtonAfterComplete={false}
                        uppy={this.uppyUploader.uppy}
                    />
                </div>
                <p className="mb-0 fw-medium text-gray-500">
                    {loading ? 'Please Wait...' : uploadedFileName || 'Your selected file name'}
                </p>
            </Modal.Body>
            <Modal.Footer>
                <Button id="close_import_btn" variant='secondary' size='sm' className="btn-min-w me-3" onClick={this.onClose}>Cancel</Button>
                <Button id="import_btn" variant='primary' size='sm' className={`btn-min-w me-3${uploadedFileName == '' ? ' disabled' : ''}`} onClick={this.onUploadCSV}>Upload and continue</Button>
            </Modal.Footer>
        </Modal>
    }
}